"use client";

import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { CartItem } from '@/components/CartItem';
import { useCart } from '@/context/CartContext';

export default function CartPage() {
  const { items, totalPrice, totalItems, clearCart } = useCart();

  if (items.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[60vh] space-y-4">
        <div className="text-center space-y-2">
          <h2 className="text-2xl font-bold text-gray-900">Your cart is empty</h2>
          <p className="text-gray-600">
            Add some delicious food from our restaurants
          </p>
        </div>
        <Link href="/">
          <Button className="bg-black text-white hover:bg-gray-800">
            Browse Restaurants
          </Button>
        </Link>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Your Cart</h1>
        <Button
          variant="outline"
          onClick={clearCart}
          className="text-red-600 border-red-600 hover:bg-red-50"
        >
          Clear Cart
        </Button>
      </div>

      <div className="space-y-4">
        {items.map((item) => (
          <CartItem key={item.id} item={item} />
        ))}
      </div>

      {/* Order Summary */}
      <Card className="sticky bottom-20 bg-white shadow-lg">
        <CardContent className="p-4">
          <div className="space-y-3">
            <div className="flex justify-between items-center">
              <span className="text-gray-600">Items ({totalItems})</span>
              <span className="font-semibold">${totalPrice.toFixed(2)}</span>
            </div>
            
            <div className="flex justify-between items-center">
              <span className="text-gray-600">Delivery Fee</span>
              <span className="font-semibold">$2.99</span>
            </div>
            
            <div className="flex justify-between items-center">
              <span className="text-gray-600">Service Fee</span>
              <span className="font-semibold">$1.99</span>
            </div>
            
            <hr className="border-gray-200" />
            
            <div className="flex justify-between items-center text-lg font-bold">
              <span>Total</span>
              <span>${(totalPrice + 2.99 + 1.99).toFixed(2)}</span>
            </div>
            
            <Link href="/payment" className="block">
              <Button className="w-full bg-black text-white hover:bg-gray-800 py-3">
                Proceed to Payment
              </Button>
            </Link>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
